import React from "react";
import BackButton from "../../images/CTA Small_Icon.png";
import Close from "../../images/close.png";
import { Link } from 'react-router-dom';
import { useContext } from "react";
import { GameStateContext } from "../helpers/Context";
import trophy from '../../images/Good_Job.png';
import Share from '../../images/share.png';
import Footer from '../../images/footer_bg.png';


const Complete = () => {
  const { score, setScore, setGameState, userName } = useContext(
    GameStateContext
  );

  const restartQuiz = () => {
    setScore(0);
    setGameState("playing");
  };


  return (
    <div
      className="body_container"
      style={{
        backgroundColor: '#F6F7FA',
        height: "100vh",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
      }}
    >
      <div style={{ display: "flex", justifyContent: "space-around", paddingTop: '30px' }}>
        <div>
          <img src={BackButton} style={{ height: '30px' }} alt='' />
        </div>
        <div>
          <h2 style={{ color: '#FF565E', fontFamily: 'Poppins', fontSize: '18px', fontWeight: '600' }}>Congratulations !</h2>
        </div>
        <div>
          <img src={Close} style={{ height: '30px' }} alt='' />
        </div>
      </div>

      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: "150px", flexDirection: 'column' }}>
        <img src={trophy} />
        <p style={{
          marginTop: '10px',
          fontFamily: 'poppins',
          fontWeight: '600',
          fontSize: '16px',
          color: '#004277',
        }}>You get +80 Quiz Points</p>
      </div>

      <div style={{
        alignItems: "center",
        position: "absolute",
        bottom: 0,
        width: "100%",
        height: "70px",
        borderTopLeftRadius: "32px",
        borderTopRightRadius: "32px",
        justifyContent: "center",
        paddingLeft: "70px",
        paddingRight: "70px",
        backgroundImage: `url(${Footer})`,
        //backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column'


      }}>
        <div style={{ display: 'flex', justifyContent: 'space-evenly', flexDirection: 'row' }}>
          <Link to={'/'} style={{ textDecoration: 'none' }} >
            <button
              style={{
                textAlign: 'center',
                backgroundColor: '#00E3B6',
                padding: '11px',
                borderRadius: '8px',
                fontWeight: 'bold',
                boxShadow: `1px 1px 4px rgba(15, 218, 137, 0.5)`,
                color: '#FFFFFF',
                fontFamily: 'poppins',
                width: '250px',
                marginTop: '12px',
                borderColor: '#00E3B6',
                height: '45px',
                marginRight: '10px'

              }}
            >
              Done
            </button>
          </Link>

          <button
            style={{
              textAlign: 'center',
              backgroundColor: 'rgba(246, 247, 250, 0.3)',
              opacity: '30%',
              padding: '11px',
              borderRadius: '8px',
              fontWeight: 'bold',

              color: '#124589',
              fontFamily: 'poppins',
              width: '50px',
              marginTop: '12px',
              height: '45px',
              border: '2px solid #00E3B6'

            }}
          >
            <img src={Share} />
          </button>

        </div>


      </div>
    </div>

  );
};

export default Complete;
